<template>
  <div id="leftNav">
   <el-col :span="12">
     <el-menu router
       :default-active="$route.path"
       class="el-menu-vertical-demo"
       background-color="#545c64"
       text-color="#fff"
       active-text-color="#ffd04b"
              :default-openeds="openeds">
         <el-submenu index="1" class="minWidth">
           <template slot="title">
             <span></span>
           </template>
           <el-menu-item-group>
             <el-menu-item index="/setManagment/smHost"></el-menu-item>
             <el-menu-item index="/setManagment/smNet"></el-menu-item>
             <el-menu-item index="/setManagment/smStorage"></el-menu-item>
             <el-menu-item index="/setManagment/smBalance"></el-menu-item>
           </el-menu-item-group>
         </el-submenu>
         <el-submenu index="2" class="minWidth">
           <template slot="title">
             <span></span>
           </template>
           <el-menu-item-group>
             <el-menu-item index="/perManagment/pmHost"></el-menu-item>
             <el-menu-item index="/perManagment/pmNet"></el-menu-item>
             <el-menu-item index="/perManagment/pmStorage"></el-menu-item>
             <el-menu-item index="/perManagment/pmBalance"></el-menu-item>
             <el-menu-item index="/perManagment/pmMonitor"></el-menu-item>
             <el-menu-item index="/perManagment/log"></el-menu-item>
           </el-menu-item-group>
         </el-submenu>
         <el-submenu index="3" class="minWidth">
           <template slot="title">
             <span></span>
           </template>
           <el-menu-item-group>
             <el-menu-item index="" @click.native="getMes1"></el-menu-item>
             <el-menu-item index="" @click.native="getMes2"></el-menu-item>
             <el-menu-item index="" @click.native="getMes3"></el-menu-item>
             <el-menu-item index="" @click.native="getMes4"></el-menu-item>
           </el-menu-item-group>
         </el-submenu>
     </el-menu>
   </el-col>
  </div>
</template>

<script>
    export default {
        name: "leftNav",
        data(){
          return {
            mes:[
              {
                message:""
              },
              {
                message:""
              }
            ],
            openeds: ['1','2'],
          }
        },
        methods:{
          getMes1:function () {
            this.$notify.info({
              title:"",
              message:""
            })
          },
          getMes2:function () {
            for(var i = 0;i<this.mes.length;i++){
              this.$notify.info({
                title:"",
                message:this.mes[i].message
              });
            }
          },
          getMes3:function () {
            this.$notify.info({
              title:"",
              message:""
            })
          },
          getMes4:function () {
            this.$notify({
              title: '',
              message: '',
              type: 'warning'
            });
          }

        }

    }
</script>

<style scoped>
  #leftNav{
    min-height:698px;
  }
  .minWidth{
    min-width: 200px
  }
</style>



